import React from "react"

const Main = (props) => {
    const styles={height: 200}
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-6 row" style={styles}>
                    {props.keystrokes.map((stroke, index) => {
                        return (
                            <button 
                            className="col-4 drum-pad" 
                            id={props.songs[index]} 
                            onClick={props.playSong} 
                            key={stroke}
                            >{stroke}
                            <audio 
                            src={`./sounds/${props.songs[index]}.wav`} 
                            className="clip" 
                            id={stroke}
                            >
                            </audio>
                            </button>
                        )
                })}
                </div>
                <div className="col-6">
                    <h2>Now playing:</h2>
                    <h4 id="display">{props.name}</h4>
                    <button onClick={props.turnOnOff}>{props.online ? "TURN OFF" : "TURN ON"}</button>
                </div>
            </div>
        </div>
    )
}

export default Main